.welcome-container {
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 50px;
  width: 100%;
  height: 100%;
  padding-top: 115px;
  padding-left: 27px;
  padding-right: 27px;
  display: flex;
}

.welcome-container > h1 {
  text-align: center;
  font-family: var(--default-font-family);
  color: #009048;
  word-break: break-word;
  margin: 0;
  font-size: clamp(40px, 8vw, 80px);
  font-weight: 700;
  line-height: 1;
  overflow: visible;
}

.welcome-container span {
  color: #91ccaf;
}

.contenedor-rules {
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 50px;
  width: 100%;
  height: 100%;
  padding-left: 27px;
  padding-right: 27px;
  display: flex;
}

.contenedor-rules-texto {
  text-align: center;
  max-width: 980px;
  font-family: American Typewriter, Courier New, Courier, monospace;
  font-size: 40px;
  font-weight: 600;
  line-height: 100%;
  display: block;
  overflow: hidden;
}

mi-boton {
  width: 100%;
  max-width: 404px;
}

.contenedor-game {
  grid-template-rows: 2fr 1fr;
  grid-template-columns: 1fr;
  width: 100%;
  height: 100%;
  display: grid;
}

.contenedor-contador {
  justify-content: center;
  align-items: center;
  display: flex;
}

.result-container {
  justify-content: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.hands-container {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.hands-container single-hand:first-child {
  transform: rotate(180deg);
}

.star-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.result {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: 100%;
  display: flex;
}

.result.win {
  background-color: #88894999;
}

.result.lose {
  background-color: #89494999;
}

.result.draw {
  background-color: #81816e99;
}
